.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}





*,*:after,*:before {box-sizing:border-box;}
body {font-size:0.9rem;}
a {text-decoration:none;color:#333;}
#header {height:50px;position:relative;
    line-height:50px;/* border:1px solid red; */}
#header h1 {position:absolute;left:0;
display:inline-block;}
#header h1 a img {width:50px;}

#head {position:relative;}

.cross{
  background:none;
  position:absolute;
  top:0px;
 left:100px;
  padding:0 15px 0;
  color:#666;
  border:0;
  font-size:3.5rem;
  line-height:65px;
  font-weight:bold;
  cursor:pointer;
  outline:none;
  z-index:100000;
  display:none;
}


#menu {text-align: right;    }
#menu a {display:inline-block;margin-right:10px;font-weight: bold;}
#menu button {border:none; background:none;vertical-align: middle;}
button img {width:60%;}
#main img {width:100%;}
#slide {width:100%;position:relative;overflow:hidden;}
#slide img {width:100%;}
#slide ul {position:relative; left:0;overflow:hidden;width:300%;}
#slide ul li {width:33.33%;float:left;}

#roll {position:absolute;bottom:5px;left:50%;transform:translateX(-50%);}
#roll a {display:inline-block;width:10px;height:10px;border-radius:5px;background: #fff;
margin-right:5px;}
#roll a:nth-child(3) {margin-right:0;}

#list {width:95%; margin:0 auto 3rem;position:relative;padding-bottom:3rem;}
#list li {float:left;width:50%;padding-right:3px;}
#list h3 {text-align: center;margin:2rem 0;}
span {display: block;height:20px;}
.im {margin:10px 0 8px 0;}
.im img {height:18px;}
.txt1 {font-size:10px;}
#list  ul>li> a>img {width:100%;}

.menu {z-index: 100000; font-weight: bold; font-size: 0.8rem; 
  height:100%; position: fixed;width:75%;top:0;
background:#fff;
  /* right:0; */right:-75%;} 


.menu ul {padding:0 10px 0;height:100%;}
.menu .mm >li {padding:15px 0; border-bottom:1px solid #ece6e3;position: relative;
     text-indent: 20px;}       
.mm li img {position: absolute;right:10px;top:15px;width:15px;}          
.menu ul.mm li a {color:#483834;font-size:13px;}
.sub {text-align: center; display: none;}
.sub li {padding:0;}
.sub li a {padding:3px 0;display:block;}	   

.head {height:50px;background: #fe7c98;line-height:50px;}
.head>ul>li {margin-right:1rem;}
.head>ul>li:last-child {margin-right:0;}	   
.fr {padding-left:10px;}	

.head ul li {float:left;}	   
.head ul li.search {float:right;text-align:right;width:10rem;
padding-top:13px;}	   
.search img {width:55%;}
.head a {color:#fff;}

.sub {padding:0 15px; }
.sub >li {height:40px;line-height:40px;text-align:left;text-indent:15px;}
.sub li {border-bottom:1px solid #ece6e3;}
